"use strict"
module.exports = function localize_ru(errors) {
  if (!(errors && errors.length)) return
  for (const e of errors) {
    let out
    switch (e.keyword) {
      case "additionalItems":
      case "items":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n >= 2 && n <= 4) {
          out += ""
        } else if (n != 1) {
          out += ""
        }
        break
      case "additionalProperties":
        out = "    "
        break
      case "anyOf":
        out = '      "anyOf"'
        break
      case "const":
        out = "    "
        break
      case "contains":
        out = "    "
        break
      case "dependencies":
      case "dependentRequired":
        out = ""
        var n = e.params.depsCount
        out += "  "
        if (n == 1) {
          out += ""
        } else {
          out += ""
        }
        out +=
          " " + e.params.deps + ",    " + e.params.property
        break
      case "discriminator":
        switch (e.params.error) {
          case "tag":
            out = ' "' + e.params.tag + '"   '
            break
          case "mapping":
            out =
              '  "' +
              e.params.tag +
              '"      oneOf  '
            break
          default:
            out = '   "' + e.keyword + '"'
        }
        break
      case "enum":
        out = "      "
        break
      case "false schema":
        out = "  false"
        break
      case "format":
        out = '   "' + e.params.format + '"'
        break
      case "formatMaximum":
      case "formatExclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "  " + cond
        break
      case "formatMinimum":
      case "formatExclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "  " + cond
        break
      case "if":
        out = '  e "' + e.params.failingKeyword + '"'
        break
      case "maximum":
      case "exclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "  " + cond
        break
      case "maxItems":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n >= 2 && n <= 4) {
          out += ""
        } else if (n != 1) {
          out += ""
        }
        break
      case "maxLength":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n >= 2 && n <= 4) {
          out += ""
        } else if (n != 1) {
          out += ""
        }
        break
      case "maxProperties":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n == 1) {
          out += ""
        } else if (n >= 2 && n <= 4) {
          out += ""
        } else {
          out += ""
        }
        break
      case "minimum":
      case "exclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "  " + cond
        break
      case "minItems":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n >= 2 && n <= 4) {
          out += ""
        } else if (n != 1) {
          out += ""
        }
        break
      case "minLength":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n >= 2 && n <= 4) {
          out += ""
        } else if (n != 1) {
          out += ""
        }
        break
      case "minProperties":
        out = ""
        var n = e.params.limit
        out += "   ,  " + n + " "
        if (n == 1) {
          out += ""
        } else if (n >= 2 && n <= 4) {
          out += ""
        } else {
          out += ""
        }
        break
      case "multipleOf":
        out = "   " + e.params.multipleOf
        break
      case "not":
        out = '     "not"'
        break
      case "oneOf":
        out = '     e  "oneOf"'
        break
      case "pattern":
        out = '   "' + e.params.pattern + '"'
        break
      case "patternRequired":
        out =
          '  ,   "' +
          e.params.missingPattern +
          '"'
        break
      case "propertyNames":
        out = "    "
        break
      case "required":
        out = "    " + e.params.missingProperty
        break
      case "type":
        out = "  " + e.params.type
        break
      case "unevaluatedItems":
        out = ""
        var n = e.params.len
        out += "   ,  " + n + " "
        if (n >= 2 && n <= 4) {
          out += ""
        } else if (n != 1) {
          out += ""
        }
        break
      case "unevaluatedProperties":
        out = "    "
        break
      case "uniqueItems":
        out =
          "     ( " +
          e.params.j +
          "  " +
          e.params.i +
          " )"
        break
      default:
        out = '   "' + e.keyword + '"'
    }
    e.message = out
  }
}
