"use strict"
module.exports = function localize_fi(errors) {
  if (!(errors && errors.length)) return
  for (const e of errors) {
    let out
    switch (e.keyword) {
      case "additionalItems":
      case "items":
        out = ""
        var n = e.params.limit
        out += "saa sislt enintn " + n
        if (n == 1) {
          out += ":n elementin"
        } else {
          out += " elementti"
        }
        break
      case "additionalProperties":
        out = "ei saa sislt ylimrisi ominaisuuksia"
        break
      case "anyOf":
        out = 'tytyy vastata "anyOf" skeemaa'
        break
      case "const":
        out = "tytyy olla yht kuin vakio"
        break
      case "contains":
        out = "tytyy sislt kelvollinen elementti"
        break
      case "dependencies":
      case "dependentRequired":
        out = ""
        var n = e.params.depsCount
        out += "tytyy sislt " + e.params.deps + " ominaisuu"
        if (n == 1) {
          out += "s"
        } else {
          out += "det"
        }
        out += " kun " + e.params.property + "-ominaisuus on lsn"
        break
      case "discriminator":
        switch (e.params.error) {
          case "tag":
            out = 'tunniste "' + e.params.tag + '" tytyy olla merkkijono'
            break
          case "mapping":
            out =
              'tunnisteen "' + e.params.tag + '" arvon muoto pit olla oneOf'
            break
          default:
            out = 'tytyy lpist "' + e.keyword + '" avainsanatarkistus'
        }
        break
      case "enum":
        out = "tytyy olla yht kuin jokin sallituista arvoista"
        break
      case "false schema":
        out = "boolean skeema on vr"
        break
      case "format":
        out = 'tytyy vastata muotoa "' + e.params.format + '"'
        break
      case "formatMaximum":
      case "formatExclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "tytyy olla " + cond
        break
      case "formatMinimum":
      case "formatExclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "tytyy olla " + cond
        break
      case "if":
        out = 'tytyy vastata "' + e.params.failingKeyword + '" skeemaa'
        break
      case "maximum":
      case "exclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "tytyy olla " + cond
        break
      case "maxItems":
        out = ""
        var n = e.params.limit
        out += "tulee sislt enintn " + n + " "
        if (n == 1) {
          out += "elementti"
        } else {
          out += "elementti"
        }
        break
      case "maxLength":
        out = ""
        var n = e.params.limit
        out += "ei saa olla pidempi kuin " + n + " merkki"
        if (n != 1) {
          out += ""
        }
        break
      case "maxProperties":
        out = ""
        var n = e.params.limit
        out += "tulee sislt enintn " + n + " "
        if (n == 1) {
          out += "ominaisuus"
        } else {
          out += "ominaisuutta"
        }
        break
      case "minimum":
      case "exclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "tytyy olla " + cond
        break
      case "minItems":
        out = ""
        var n = e.params.limit
        out += "tulee sislt vhintn " + n + " "
        if (n == 1) {
          out += "elementti"
        } else {
          out += "elementti"
        }
        break
      case "minLength":
        out = ""
        var n = e.params.limit
        out += "ei saa olla lyhyempi kuin " + n + " merkki"
        if (n != 1) {
          out += ""
        }
        break
      case "minProperties":
        out = ""
        var n = e.params.limit
        out += "tulee sislt vhintn " + n + " "
        if (n == 1) {
          out += "ominaisuus"
        } else {
          out += "ominaisuutta"
        }
        break
      case "multipleOf":
        out = "tytyy olla moninkertainen: " + e.params.multipleOf
        break
      case "not":
        out = 'ei saa olla hyvksytty skeeman "not" mukaan'
        break
      case "oneOf":
        out =
          'tytyy vastata tsmlleen yht "oneOf" -kohdassa mritelty skeemaa'
        break
      case "pattern":
        out = 'tytyy vastata muotoa "' + e.params.pattern + '"'
        break
      case "patternRequired":
        out =
          'tytyy sislt ominaisuus joka vastaa kaavaa "' +
          e.params.missingPattern +
          '"'
        break
      case "propertyNames":
        out = "ominaisuuden nimi on virheellinen"
        break
      case "required":
        out = "tytyy sislt vaadittu ominaisuus " + e.params.missingProperty
        break
      case "type":
        out = ""
        var t = e.params.type
        out += "tytyy olla "
        if (t == "number") {
          out += "numero"
        } else if (t == "integer") {
          out += "kokonaisluku"
        } else if (t == "string") {
          out += "merkkijono"
        } else if (t == "boolean") {
          out += "boolean"
        } else {
          out += t
        }
        break
      case "unevaluatedItems":
        out = ""
        var n = e.params.len
        out += "ei saa olla enemmn kuin " + n + " elementti"
        if (n != 1) {
          out += ""
        }
        break
      case "unevaluatedProperties":
        out = "ei saa sislt arvioimattomia ominaisuuksia"
        break
      case "uniqueItems":
        out =
          "ei saa sislt duplikaatteja (elementit ## " +
          e.params.j +
          " ja " +
          e.params.i +
          " ovat identtiset)"
        break
      default:
        out = 'tytyy lpist "' + e.keyword + '" avainsanatarkistus'
    }
    e.message = out
  }
}
