"use strict"
module.exports = function localize_pl(errors) {
  if (!(errors && errors.length)) return
  for (const e of errors) {
    let out
    switch (e.keyword) {
      case "additionalItems":
      case "items":
        out = ""
        var n = e.params.limit
        out += "nie powinien mie wicej ni " + n + " element"
        if (n == 1) {
          out += "u"
        } else {
          out += "w"
        }
        break
      case "additionalProperties":
        out = "nie powinien zawiera dodatkowych pl"
        break
      case "anyOf":
        out = 'powinien pasowa do wzoru z sekcji "anyOf"'
        break
      case "const":
        out = "powinien by rwny staej"
        break
      case "contains":
        out = "must contain a valid item"
        break
      case "dependencies":
      case "dependentRequired":
        out = ""
        var n = e.params.depsCount
        out += "powinien zawiera pol"
        if (n == 1) {
          out += "e"
        } else {
          out += "a"
        }
        out +=
          " " +
          e.params.deps +
          " kiedy pole " +
          e.params.property +
          " jest obecne"
        break
      case "discriminator":
        switch (e.params.error) {
          case "tag":
            out = 'tag "' + e.params.tag + '" must be string'
            break
          case "mapping":
            out = 'value of tag "' + e.params.tag + '" must be in oneOf'
            break
          default:
            out = 'powinien przej walidacj "' + e.keyword + '"'
        }
        break
      case "enum":
        out = "powinien by rwny jednej z predefiniowanych wartoci"
        break
      case "false schema":
        out = "boolean schema is false"
        break
      case "format":
        out = 'powinien zgadza si z formatem "' + e.params.format + '"'
        break
      case "formatMaximum":
      case "formatExclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "powinien by " + cond
        break
      case "formatMinimum":
      case "formatExclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "powinien by " + cond
        break
      case "if":
        out = 'must match "' + e.params.failingKeyword + '" schema'
        break
      case "maximum":
      case "exclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "powinien by " + cond
        break
      case "maxItems":
        out = ""
        var n = e.params.limit
        out += "nie powinien mie wicej ni " + n + " element"
        if (n == 1) {
          out += "u"
        } else {
          out += "w"
        }
        break
      case "maxLength":
        out = ""
        var n = e.params.limit
        out += "nie powinien by duszy ni " + n + " znak"
        if (n != 1) {
          out += "w"
        }
        break
      case "maxProperties":
        out = ""
        var n = e.params.limit
        out += "nie powinien zawiera wicej ni " + n + " "
        if (n == 1) {
          out += "pole"
        } else {
          out += "pl"
        }
        break
      case "minimum":
      case "exclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "powinien by " + cond
        break
      case "minItems":
        out = ""
        var n = e.params.limit
        out += "nie powinien mie mniej ni " + n + " element"
        if (n == 1) {
          out += "u"
        } else {
          out += "w"
        }
        break
      case "minLength":
        out = ""
        var n = e.params.limit
        out += "nie powinien by krtszy ni " + n + " znak"
        if (n != 1) {
          out += "w"
        }
        break
      case "minProperties":
        out = ""
        var n = e.params.limit
        out += "nie powinien zawiera mniej ni " + n + " "
        if (n == 1) {
          out += "pole"
        } else {
          out += "pl"
        }
        break
      case "multipleOf":
        out = "powinien by wielokrotnoci " + e.params.multipleOf
        break
      case "not":
        out = 'nie powinien pasowa do wzoru z sekcji "not"'
        break
      case "oneOf":
        out = 'powinien pasowa do jednego wzoru z sekcji "oneOf"'
        break
      case "pattern":
        out = 'powinien zgadza si ze wzorem "' + e.params.pattern + '"'
        break
      case "patternRequired":
        out =
          'powinien mie pole pasujce do wzorca "' +
          e.params.missingPattern +
          '"'
        break
      case "propertyNames":
        out = "property name is invalid"
        break
      case "required":
        out = "powinien zawiera wymagane pole " + e.params.missingProperty
        break
      case "type":
        out = "powinien by " + e.params.type
        break
      case "unevaluatedItems":
        out = ""
        var n = e.params.len
        out += "must NOT have more than " + n + " item"
        if (n != 1) {
          out += "s"
        }
        break
      case "unevaluatedProperties":
        out = "must NOT have unevaluated properties"
        break
      case "uniqueItems":
        out =
          "nie powinien zawiera elementw ktre si powtarzaj (elementy " +
          e.params.j +
          " i " +
          e.params.i +
          " s identyczne)"
        break
      default:
        out = 'powinien przej walidacj "' + e.keyword + '"'
    }
    e.message = out
  }
}
