"use strict"
module.exports = function localize_cs(errors) {
  if (!(errors && errors.length)) return
  for (const e of errors) {
    let out
    switch (e.keyword) {
      case "additionalItems":
      case "items":
        out = ""
        var n = e.params.limit
        out += "neme mt vc, ne " + n + " prv"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "ek"
        } else {
          out += "k"
        }
        break
      case "additionalProperties":
        out = "neme mt dal poloky"
        break
      case "anyOf":
        out = 'mus vyhovt alespo jednomu schmatu v "anyOf"'
        break
      case "const":
        out = "mus bt roven konstant"
        break
      case "contains":
        out = "mus obsahovat prvek odpovdajc schmatu"
        break
      case "dependencies":
      case "dependentRequired":
        out = ""
        var n = e.params.depsCount
        out += "mus mt polo"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "ek"
        } else {
          out += "ka"
        }
        out += ": " + e.params.deps + ", pokud obsahuje " + e.params.property
        break
      case "discriminator":
        switch (e.params.error) {
          case "tag":
            out = 'tag "' + e.params.tag + '" must be string'
            break
          case "mapping":
            out = 'value of tag "' + e.params.tag + '" must be in oneOf'
            break
          default:
            out = 'mus vyhovt "' + e.keyword + '" validaci'
        }
        break
      case "enum":
        out = "mus bt rovno jedn hodnot z vtu"
        break
      case "false schema":
        out = "schma je false"
        break
      case "format":
        out = 'mus bt ve formtu "' + e.params.format + '"'
        break
      case "formatMaximum":
      case "formatExclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "mus bt " + cond
        break
      case "formatMinimum":
      case "formatExclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "mus bt " + cond
        break
      case "if":
        out = 'mus vyhovt "' + e.params.failingKeyword + '" schmatu'
        break
      case "maximum":
      case "exclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "mus bt " + cond
        break
      case "maxItems":
        out = ""
        var n = e.params.limit
        out += "nesm obsahovat vc ne " + n + " prv"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "ek"
        } else {
          out += "k"
        }
        break
      case "maxLength":
        out = ""
        var n = e.params.limit
        out += "nesm bt del ne " + n + " zna"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "k"
        } else {
          out += "k"
        }
        break
      case "maxProperties":
        out = ""
        var n = e.params.limit
        out += "nesm mt vc ne " + n + " polo"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "ek"
        } else {
          out += "ka"
        }
        break
      case "minimum":
      case "exclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "mus bt " + cond
        break
      case "minItems":
        out = ""
        var n = e.params.limit
        out += "nesm obsahovat mn ne " + n + " prv"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "ek"
        } else {
          out += "k"
        }
        break
      case "minLength":
        out = ""
        var n = e.params.limit
        out += "nesm bt krat ne " + n + " zna"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "k"
        } else {
          out += "k"
        }
        break
      case "minProperties":
        out = ""
        var n = e.params.limit
        out += "nesm mt mn ne " + n + " polo"
        if (n >= 2 && n <= 4) {
          out += "ky"
        } else if (n != 1) {
          out += "ek"
        } else {
          out += "ka"
        }
        break
      case "multipleOf":
        out = "mus bt nsobkem " + e.params.multipleOf
        break
      case "not":
        out = 'nesm vyhovt schmatu v "not"'
        break
      case "oneOf":
        out = 'mus vyhovt prv jednomu schmatu v "oneOf"'
        break
      case "pattern":
        out = 'mus vyhovt regulrnmu vrazu "' + e.params.pattern + '"'
        break
      case "patternRequired":
        out =
          'mus obsahovat poloku vyhovujc regulrnmu vrazu "' +
          e.params.missingPattern +
          '"'
        break
      case "propertyNames":
        out = "nzev poloky nen platn"
        break
      case "required":
        out = "mus obsahovat poadovanou poloku " + e.params.missingProperty
        break
      case "type":
        out = "mus bt " + e.params.type
        break
      case "unevaluatedItems":
        out = ""
        var n = e.params.len
        out += "must NOT have more than " + n + " item"
        if (n != 1) {
          out += "s"
        }
        break
      case "unevaluatedProperties":
        out = "must NOT have unevaluated properties"
        break
      case "uniqueItems":
        out =
          "nesm obsahovat duplicitn prvky (prvky ## " +
          e.params.j +
          " a " +
          e.params.i +
          " jsou identick)"
        break
      default:
        out = 'mus vyhovt "' + e.keyword + '" validaci'
    }
    e.message = out
  }
}
