"use strict"
module.exports = function localize_ja(errors) {
  if (!(errors && errors.length)) return
  for (const e of errors) {
    let out
    switch (e.keyword) {
      case "additionalItems":
      case "items":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "additionalProperties":
        out = ""
        break
      case "anyOf":
        out = '"anyOf"'
        break
      case "const":
        out = "must be equal to constant"
        break
      case "contains":
        out = "must contain a valid item"
        break
      case "dependencies":
      case "dependentRequired":
        out = "" + e.params.property + ""
        var n = e.params.depsCount
        out += "" + e.params.deps + ""
        break
      case "discriminator":
        switch (e.params.error) {
          case "tag":
            out = 'tag "' + e.params.tag + '" must be string'
            break
          case "mapping":
            out = 'value of tag "' + e.params.tag + '" must be in oneOf'
            break
          default:
            out = 'must pass "' + e.keyword + '" keyword validation'
        }
        break
      case "enum":
        out = ""
        break
      case "false schema":
        out = "boolean schema is false"
        break
      case "format":
        out = '"' + e.params.format + '"'
        break
      case "formatMaximum":
      case "formatExclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "must be " + cond
        break
      case "formatMinimum":
      case "formatExclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += "must be " + cond
        break
      case "if":
        out = 'must match "' + e.params.failingKeyword + '" schema'
        break
      case "maximum":
      case "exclusiveMaximum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += cond + ""
        break
      case "maxItems":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "maxLength":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "maxProperties":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "minimum":
      case "exclusiveMinimum":
        out = ""
        var cond = e.params.comparison + " " + e.params.limit
        out += cond + ""
        break
      case "minItems":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "minLength":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "minProperties":
        out = ""
        var n = e.params.limit
        out += "" + n + ""
        break
      case "multipleOf":
        out = "" + e.params.multipleOf + ""
        break
      case "not":
        out = '"not"'
        break
      case "oneOf":
        out = '"oneOf"'
        break
      case "pattern":
        out = '"' + e.params.pattern + '"'
        break
      case "patternRequired":
        out =
          'must have property matching pattern "' +
          e.params.missingPattern +
          '"'
        break
      case "propertyNames":
        out = "property name is invalid"
        break
      case "required":
        out =
          "" + e.params.missingProperty + ""
        break
      case "type":
        out = "" + e.params.type + ""
        break
      case "unevaluatedItems":
        out = ""
        var n = e.params.len
        out += "must NOT have more than " + n + " item"
        if (n != 1) {
          out += "s"
        }
        break
      case "unevaluatedProperties":
        out = "must NOT have unevaluated properties"
        break
      case "uniqueItems":
        out =
          "" +
          e.params.j +
          "" +
          e.params.i +
          ""
        break
      default:
        out = 'must pass "' + e.keyword + '" keyword validation'
    }
    e.message = out
  }
}
